import { Component, ChangeDetectionStrategy, ChangeDetectorRef } from "@angular/core";
import { Platform, ModalController, App, NavController } from "ionic-angular";


import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";
import { AppConstants } from "@app/app.constants";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";
import { Backend } from "@backend/index";
import { MarsAuthService } from "@services/auth.service";
import { CurrencyPipe } from "@angular/common";


@Component({
    selector: "page-payment-information",
    templateUrl: "payment-information.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class PaymentInformationPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;
    paymentInstruments: Array<any>;
    subscriptionPrice: number;
    token: string;
    isMerchant: any;
    currency: CurrencyPipe;

    constructor(private platform: Platform,
        private app: App,
        private navCtrl: NavController,
        private modalCtrl: ModalController,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private globals: AppGlobals,
        private interactionService: MarsInteractionService) {
        this.navigationService = new MarsNavigationService(this.app as any);
        this.navigationService.setNavCtrl(this.navCtrl as any);
        this.translations = this.locales.load();
        this.currency = new CurrencyPipe('pt-BR');
    }

    ionViewWillEnter() {
        this.init();
    }

    async init() {
        if (MarsAuthService.isLoggedIn()) {
            this.token = MarsAuthService.getMarsToken();
            this.isMerchant = MarsAuthService.hasRole('MERCHANT');
            this.getSubscriptionPlans();
            this.getPaymentInstruments();
            this.subscriptionPrice = 79.99;
        }
    }

    async getPaymentInstruments() {
        try {
            let customerId = MarsAuthService.getLoggedInUser()._id;
            this.paymentInstruments = (await Backend.getPaymentCards({ id: customerId, xAccessToken: this.token })).data;
        } catch (e) {
            this.paymentInstruments = [];
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    async add() {
        let creationModal = this.modalCtrl.create("PaymentCardPage");
        creationModal.present();
        creationModal.onWillDismiss(async ($event) => {
            let instruments = $event.instruments;
            if (instruments) await this.getPaymentInstruments();
            if ($event.card && this.isMerchant) await this.subscribeToPlan($event.card);
            if (!MarsAuthService.finishedSignup() && (!this.isMerchant || ($event.card && this.isMerchant))) await this.finishSignupProcess();
            this.changeDetector.detectChanges();
        });
    }

    async remove(card) {
        let remove = async () => {
            let spinner = this.interactionService.spinner({ content: this.translations.loading + "..." })
            try {
                this.paymentInstruments = (await Backend.removePaymentCard({ id: card.id, xAccessToken: this.token })).data;
            } catch (e) {
                this.interactionService.alert(this.translations.server_failure);
            } finally {
                spinner.dismiss();
                this.changeDetector.detectChanges();
            }
        }
        this.interactionService.confirm(this.translations.do_you_really_want_to_remove_this_card,
            [{ text: this.translations.no, role: "cancel", handler: () => { } },
            { text: this.translations.yes, cssClass: "strong", handler: remove }]);
    }

    async getSubscriptionPlans() {
        try {
            let plans = (await Backend.getSubscriptionPlans()).data;
        } catch (e) {
            
        }
    }

    async subscribeToPlan(paymentCard: any) {
        let user = MarsAuthService.getLoggedInUser();
        let userSubscribedToPlan = user.payment && user.payment.subscriber;
        if (MarsAuthService.hasRole('MERCHANT') && !userSubscribedToPlan) {
            let user = MarsAuthService.getLoggedInUser();
            let hasSubscribed = user && user.payment && user.payment.subcription;
            if (!hasSubscribed) {
                try {
                    let subscription = (await Backend.addSubscription({
                        subscription: {
                            instrument: paymentCard, amount: this.subscriptionPrice
                        }, xAccessToken: this.token
                    })).data;
                } catch (e) {
                    
                }
            }
        }
    };

    async finishSignupProcess() {
        let spinner = this.interactionService.spinner({ content: `${this.translations.loading}...` });
        try {
            let user = MarsAuthService.getLoggedInUser();
            let role = user.roles[0].toLowerCase();
            user.signupStep = "finished";
            await Backend.updateUser({ xAccessToken: this.token, user: user });
            MarsAuthService.setLoggedInUser(user);
            this.interactionService.toast(this.translations.welcome);
            this.navigationService.goToRootPage();
        } catch (e) {
            
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            spinner.dismiss();
        }
    }

    getPricingMessage() {
        return this.translations.the_current_subscription_value_is + `<b> ${this.currency.transform(this.subscriptionPrice, 'BRL')}.</b>`;
    }

    async exit() {
        await this.finishSignupProcess();
        return this.navigationService.canGoBack() ? this.navigationService.goBack() : this.navigationService.goToRootPage();
    }
}



